import React from "react"
import { Grid } from '@mui/material'
import MuiTypography from '@mui/material/Typography'

import MainCard from './../../../ui-component/cards/MainCard'

import { useTheme } from '@mui/material/styles';

function AboutZh() {
  const theme = useTheme()
  return (
    <MainCard title={'项目介绍'}>   
      <Grid container direction="column" spacing={1}>
        <Grid item xs={12}>
          一个纯客户端项目，支持ethereum上NFT的批量铸造，目前所有功能免费，欢迎使用反馈。
        </Grid>
        <Grid item xs={12}>
          <MuiTypography variant="h3" gutterBottom  sx= {{my:3,color:theme.palette.secondary.main}}>
           一. 网站功能
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            1. 通过合约方法批量铸造NFT。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            2. 使用十六进制数据批量铸造NFT。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            3. 批量跟单功能(提供metamask跟单、十六进制批量跟单)。
          </MuiTypography> 
          <MuiTypography variant="body1" gutterBottom>
            4. NFT批量转移。
          </MuiTypography>  
          <MuiTypography variant="body1" gutterBottom>
            5. 批量生成钱包。
          </MuiTypography> 
       
          <MuiTypography variant="h3" gutterBottom sx= {{my:3,color:theme.palette.secondary.main}}>
           二. 安全问题
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            1. 项目方不会接触用户的账号私钥。
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
            2. 由于批量铸造功能需要导入私钥，用户务必注意私钥使用安全问题，尽量不要使用电脑的粘贴复制功能。
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
            3. 用于批量mint的账号保存尽量小的金额,比如小于0.01。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            4. 铸造完成后尽量将有价值的NFT转移到自己使用的主钱包。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            5. 尽可能的定期更换使用的小号钱包，使用过的钱包建议直接标记为废钱包，保存到特别位置，避免误作他用。
          </MuiTypography> 
          <MuiTypography variant="body1" gutterBottom>
            6. 连接不同的公共rpc节点都是安全的
          </MuiTypography> 
          
       
          <MuiTypography variant="h3" gutterBottom sx= {{my:3,color:'#5e35b1'}}>
           三. 简要使用说明
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            1. 当前使用不是必须连接钱包。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            2. 现在Ethereum的链上交互，本质上就是去调用链上合约的某个函数或者说方法，带参数或者不带参数。
          </MuiTypography>                       
          <MuiTypography variant="body1" gutterBottom>
            3. 跟单铸造功能可以选择直接Mint和通过Metamask铸造两种方式，前者支持批量铸造，需要导入私钥，如果开启自动跟单则还需要导入被跟单的地址列表，可以设置小费价格，默认为被跟单交易小费价格的1.1倍；
               后者需要连接metamask钱包，不需要导入私钥，支持单一交互。             
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
            4. 合约铸造功能需要设置需要操作的合约的地址、小费价格、最大Gas价格、需要发送的Eth数量(对于铸造NFT来说，一般是NFT的铸造价格)、交互函数名、交互函数参数类型以及参数值，都不可缺少。
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
            3. 十六进制铸造功能本质上也是跟单，单一的跟单，在Hash文本框输入需要跟的某交易的Hash，应用会获取相应的合约地址和十六进制数据(其中包含交互函数的相关信息)，另外也可以设置小费价格、Gas最大价格、需要发送的Eth数量(对铸造NFT来说，一般是NFT的铸造价格)，也可以不修改。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            4. 归集NFT功能可以把上面批量铸造获得的NFT全部转移到一个钱包，便于管理，需要设置要操作的NFT的合约地址、小费价格、最大Gas价格、需要转到的钱包地址、还需要导入需要转移的钱包的私钥。
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
            5. 批量生成钱包功能可以为你批量生成钱包和私钥，包含在一个文本文件当中，附加了一些符号，使用时可以替换掉。
          </MuiTypography>  
          <MuiTypography variant="body1" gutterBottom>
            6. 导入私钥是从指定的包含有私钥的文本文件中提取导入。我们的应用没有做存储，所以每次都要导入。
          </MuiTypography>                   
        </Grid> 
      </Grid>  
    </MainCard>  
  );
}

export default AboutZh