import React from "react"
import { Grid } from '@mui/material'
import MuiTypography from '@mui/material/Typography'

import MainCard from 'ui-component/cards/MainCard'

import { useTheme } from '@mui/material/styles';

function AboutEn() {
  const theme = useTheme()
  return (
    <MainCard title={'About Project'}>   
      <Grid container direction="column" spacing={1}>
        <Grid item xs={12}>
          A pure client, It's Free now.Welcome to use!
        </Grid>
        <Grid item xs={12}>
          <MuiTypography variant="h3" gutterBottom  sx= {{my:3,color:theme.palette.secondary.main}}>
           1.Site Features
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (1). MintNFT -- Batch mint NFT by contract method.
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (2). MintOxData -- Batch mint NFT by Hash.
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (3). FollowMint -- follow a transaction Hash and Batch mint.
          </MuiTypography> 
          <MuiTypography variant="body1" gutterBottom>
          (4). ColletNFT -- Transfer NFT.
          </MuiTypography>  
          <MuiTypography variant="body1" gutterBottom>
          (5). GenerateAddress -- Batch build wallet addresses.
          </MuiTypography> 
       
          <MuiTypography variant="h3" gutterBottom sx= {{my:3,color:theme.palette.secondary.main}}>
           2. security
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (1). Project Team never touch user's private keys.
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
          (2). Base of needing import private key list to use some features,Pleast protect you private keys,Don't use 'copy/paste' as possible.
          </MuiTypography>          
          <MuiTypography variant="body1" gutterBottom>
          (3). Please save small amount eth in your account, such as less than 0.01.
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (4). Transfer your valuable NFT to you main wallet address after you mint it.
          </MuiTypography>
          <MuiTypography variant="body1" gutterBottom>
          (5). Change your subwallet regularly,save them as useless,Don't use them anymore!
          </MuiTypography> 
          <MuiTypography variant="body1" gutterBottom>
          (6). Connect to any public RPC nodes is safe.
          </MuiTypography>                  
        </Grid> 
      </Grid>  
    </MainCard>  
  );
}

export default AboutEn