import React from "react"
import { useTranslation } from 'react-i18next';
import AboutZh from "./lang/AboutZh";
import AboutEn from "./lang/AboutEn";

function About() {
  const { i18n } = useTranslation()
  return (
    <>
     {i18n.language=='en' && <AboutEn/>}
     {i18n.language=='zh' && <AboutZh/>}    
    </> 
  );
}

export default About